<template>
  <div class="icon" v-for="icon in data" :key="icon.id">
    <div class="icon__numb">{{ icon.id }}</div>
    <h4 class="icon__title">{{ icon.title }}</h4>
    <div class="icon__row">
      <div class="icon__row-item">
        <div class="icon__img"><img :src="icon.image" :alt="img" /></div>
      </div>
      <div class="icon__row-item">
        <div class="sub">автор:</div>
        <div v-for="item in icon.creator" class="value">
          <div>{{ item }}</div>
        </div>
        <div class="sub">Количество эпизодов:</div>
        <div class="value">{{ icon.episodes }}</div>
        <div class="sub">Год выпуска:</div>
        <div class="value">{{ icon.year }}</div>
        <div class="sub">Жанр:</div>
        <div v-for="item in icon.genre" class="value">{{ item }}</div>
        <div class="sub">Длительность:</div>
        <div class="value">{{ icon.runtime_in_minutes }} min</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [],
    };
  },
  created() {
    const baseURL = "https://api.sampleapis.com/cartoons/cartoons2D";
    const dataBase = fetch(baseURL);
    dataBase
      .then((rest) => rest.json())
      .then((data) => {
        this.data = data;
        console.log(this.data);
      });
  },
};
</script>

<style lang='scss'>
.icon {
  max-width: 400px;
  min-height: 100%;
  font-size: 16px;
  color: #000;
  margin: 10px auto;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 10px;
  background: #fff;
  flex: 1 1 100%;
}
.icon__numb{
   width: 40px;
   height: 40px;
   border-radius: 50%;
   background: #333;
   color: #fff;
   display: flex;
   align-items: center;
   justify-content: center;
   margin: 10px auto;
}
.icon__title {
  font-size: 20px;
  font-weight:600;
  margin-bottom: 10px;
}
.icon__img {
   max-width: 200px;
   margin-right: 5px;
  img {
    width: 100%;
  }
  margin-bottom: 10px;
}
.icon__row{
   display: flex;
   align-items: center;
   justify-content: center;
  flex: 1 1 100%;
}
.sub {
  font-size: 14px;
  color: #000;
  margin-bottom: 10px;
}
.value {
  font-size: 16px;
  color: red;
  margin-bottom: 10px;
}
</style>